
import { http } from '@/js_sdk/luch-request/index.js' // 全局挂载引入，配置相关在该index.js文件里修改
import {
	httpUrl
} from '@/js_sdk/tools/tool.js';
export default {
	methods:{
		wxLogin:function(){
			let _this = this
			uni.login({
				provider: 'weixin',
				success: function(loginRes) {
					if(loginRes.errMsg == 'login:ok'){
						let code = loginRes.code;
						console.log(code)
						uni.request({
							url: httpUrl+ '/api/auth/index', 
						    data: {
								'code': code
							},
						    success: (res) => {
								console.log(res)
						       var token = res.data.data
						       if (res.data.code != 1) {
						       	uni.showToast({
						       		title: res.data.info,
						       		icon: 'none'
						       	});
						       } else {
						       	// _this.login(token)
								uni.setStorage({//缓存用户登陆状态
								    key: 'userInfo',  
								    data: token  
								}) 
						       }
						    }
						});
				
					}
				}
			});
		},
	},
	globalData: {
	            showAd: 1  
	},  
	onLaunch: function() {
		let _this = this;
		uni.getStorage({
		    key: 'userInfo',
		    success: function (res) {
				
		    },
			fail() {
				_this.wxLogin()
			}
		});
		// try {
		//     let token = uni.getStorageSync('userInfo');
		// 	if(!token){
		// 		_this.wxLogin()
		// 	}
		// } catch (e) {
		//     console.log(e);
		// }
	},
	computed:{
	},
	onShow: function() {
		console.log('App Show')
	},
	onHide: function() {
		console.log('App Hide')
	}
}
